.search input:focus {
  border-bottom: 1px solid #BDC4D4;
  line-height: 22px;
  transition: 0.1s all; }

.modal-header.login-header {
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  /*Main CSS*/ }
  @media only screen and (max-device-width: 767px) {
    .modal-header.login-header .login-logo img {
      margin: 0 auto; }
    .modal-header.login-header .login-details .nav-tabs > li {
      text-align: center;
      width: 50%; }
    .modal-header.login-header .login-signup .login-inner h1 {
      font-size: 26px;
      margin-bottom: 0;
      margin-top: 10px; }
    .modal-header.login-header .login-inner .login-form input {
      font-size: 15px;
      max-width: 100%;
      padding: 15px 45px; }
    .modal-header.login-header .login-inner .form-details {
      padding: 25px; }
    .modal-header.login-header .login-inner .login-form label {
      margin-bottom: 20px;
      width: 100%; }
    .modal-header.login-header .login-inner .form-btn {
      margin: 0;
      max-width: 180px; }
    .modal-header.login-header .tab-content .tab-pane {
      padding: 20px 0; }
    .modal-header.login-header #navigation .navi a {
      font-size: 14px;
      padding: 20px;
      text-align: center; }
    .modal-header.login-header #navigation .navi i {
      margin-right: 0px; }
    .modal-header.login-header #navigation .navi a:hover,
    .modal-header.login-header #navigation .navi .active a {
      background: #122143 none repeat scroll 0 0;
      border-left: none;
      display: block;
      padding-left: 20px; }
    .modal-header.login-header header .header-top img {
      max-width: 38px !important; }
    .modal-header.login-header .v-align header {
      padding: 12px 15px; }
    .modal-header.login-header header .header-top li {
      padding-left: 13px;
      padding-right: 6px; }
    .modal-header.login-header .navbar-default .navbar-toggle {
      border-color: rgba(0, 0, 0, 0); }
    .modal-header.login-header .navbar-header .navbar-toggle {
      float: left;
      margin: 0;
      padding: 0;
      top: 12px; }
    .modal-header.login-header button,
    .modal-header.login-header html [type="button"],
    .modal-header.login-header [type="reset"],
    .modal-header.login-header [type="submit"] {
      outline: medium none; }
    .modal-header.login-header .user-dashboard .sales h2 {
      color: #8492af;
      float: left;
      font-size: 14px;
      font-weight: 600;
      margin: 0;
      padding: 13px 0 0; }
    .modal-header.login-header .user-dashboard .btn.btn-secondary.btn-lg.dropdown-toggle > span {
      font-size: 11px; }
    .modal-header.login-header .user-dashboard .sales button {
      font-size: 11px;
      padding-right: 23px; }
    .modal-header.login-header .user-dashboard .sales h2 {
      font-size: 12px; }
    .modal-header.login-header .gutter {
      padding: 0; } }
  @media only screen and (max-device-width: 992px) {
    .modal-header.login-header header .header-top li {
      padding-left: 20px !important;
      padding-right: 0; }
    .modal-header.login-header header .logo img {
      max-width: 125px !important; } }
  @media only screen and (min-device-width: 767px) and (max-device-width: 998px) {
    .modal-header.login-header .user-dashboard .header-top {
      padding-top: 5px; }
    .modal-header.login-header .user-dashboard .header-rightside {
      display: inline-block;
      float: left;
      width: 100%; }
    .modal-header.login-header .user-dashboard .header-rightside .header-top img {
      max-width: 41px !important; }
    .modal-header.login-header .user-dashboard .sales button {
      font-size: 10px; }
    .modal-header.login-header .user-dashboard .btn.btn-secondary.btn-lg.dropdown-toggle > span {
      font-size: 12px; }
    .modal-header.login-header .user-dashboard .sales h2 {
      font-size: 15px; } }
  @media only screen and (min-device-width: 998px) and (max-device-width: 1350px) {
    .modal-header.login-header #navigation .logo img {
      max-width: 130px;
      padding: 16px 0 17px;
      width: 100%; } }
